import uniListItem from "@/components/uni-ui/uni-list-item/uni-list-item.vue"
import {mapMutations,mapState} from 'vuex';
export default {
	components:{
		uniListItem
	},
	data() {
		return {
		}
	},
	computed:{
		...mapState({
			token:state=>state.user.token,
			loginStatus:state=>state.user.loginStatus,
			userInfo:state=>state.user.userInfo
		})
	}
	,
	methods: {
		...mapMutations(['logout','clearCart']),
		
		logoutEvent(){
			
			this.$H.post('/user/user/logout',{
				id:this.userInfo.id
			},{
				token:true,
				checkToken:true,
				toast:false
			}).then(res=>{
				this.logout()
				this.clearCart()
				uni.showToast({
					title:'退出成功',
					icon:'none'
				})
				uni.navigateBack({
					delta: 1
				});
			})
			
		},
		
		navigate(path){
			if(!path) return;
			uni.navigateTo({
				url:`/pages/${path}/${path}`
			})
		}
	}
}
